import { Injectable } from "@angular/core";
@Injectable()
export class pinyinOrderService {

    dic = {
        "a": "",
        "b": "",
        "c": "",
        "d": "",
        "e": "",
        "f": "",
        "g": "",
        "h": "",
        "i": "",
        "j": "",
        "k": "",
        "l": "",
        "m": "",
        "n": "",
        "o": "",
        "p": "",
        "q": "",
        "r": "",
        "s": "",
        "t": "",
        "u": "",
        "v": "",
        "w": "",
        "x": "",
        "y": "",
        "z": ""
    }
    constructor() { }
    _compareCharactor(charactor1, charactor2) {
        if (charactor1 === charactor2) {
            return 0;
        } else {
            return charactor1 < charactor2 ? -1 : 1;
        }
    }
    /**
     * a,b
     * array[a]array[b]
     */
    _exchange(array, a, b) {
        var c = array[a];
        array[a] = array[b];
        array[b] = c;
    }
    /**
     * string
     * hello->olleh
     */
    _reverse(string) {
        let arr = string.split("");
        arr.reverse();
        return arr.join("");
    }
    /**
     * wordunicode
     */
    _compareCharCode(a, b) {
        if (a.charCodeAt(0) > b.charCodeAt(0)) {
            return 1;
        } else if (a.charCodeAt(0) < b.charCodeAt(0)) {
            return -1;
        } else {
            return 0;
        }
    }

    sort(array) {
        /**
         * 
         * 
         * @returns sortedArray
         */
        for (let i = 0; i < array.length; i++) {
            let k = i;
            for (var j = i; j < array.length; j++) {
                if (this.compareWord(array[j], array[k]) < 0) {
                    k = j;
                }
            }
            this._exchange(array, i, k);
        }
        return array;
    }

    compareWord(word1, word2) {
        /**
        * @description
        * compareInPinYin
        * 
        * @returns
        * word1word12-110
        * @example
        * ======C,S
        * <
        */
        if (!word1) {
            return 1
        }

        if (!word2) {
            return -1;
        }

        if (!word1 && !word2) {
            return 0;
        }

        let weightMagicNumber = 100;//
        let weightOfWord1 = 1;//
        let weightOfWord2 = 1;//

        //i
        let i = word1.length > word2.length ? word1.length : word2.length;

        let returnValueBetweenFirstCharactor = this.compareInPinYin(word1[0], word2[0]);

        if (returnValueBetweenFirstCharactor != 0) {
            //
            return returnValueBetweenFirstCharactor;
        }

        for (let j = 0; j < i; j++) {
            if (!word1[j]) {
                weightOfWord2 += weightOfWord2 + Math.pow(weightMagicNumber, (i - j))
                break;
            }
            if (!word2[j]) {
                weightOfWord1 += weightOfWord1 + Math.pow(weightMagicNumber, (i - j))
                break;
            }
            if (this.compareInPinYin(word1[j], word2[j]) > 0) {
                weightOfWord1 += weightOfWord1 + Math.pow(weightMagicNumber, (i - j))
            } else if (this.compareInPinYin(word1[j], word2[j]) < 0) {
                weightOfWord2 += weightOfWord2 + Math.pow(weightMagicNumber, (i - j))
            }
        }

        if (weightOfWord1 > weightOfWord2) {
            return 1;
        } else if (weightOfWord1 < weightOfWord2) {
            return -1;
        } else {
            return 0;
        }

    }

    compareInPinYin(charactor1, charactor2) {
        /**
         * @description
         * 
         * ,
         * 
         * <<<
         * @returns
         * charactor1charactor2-110
         */

        let sourceCharactorPinyin = this.getCharactorFirstPinYinWord(charactor1);
        let targetCharactorPinyin = this.getCharactorFirstPinYinWord(charactor2);

        if (sourceCharactorPinyin && targetCharactorPinyin) {
            let pinyinCompareResult = this._compareCharactor(sourceCharactorPinyin, targetCharactorPinyin);
            if (pinyinCompareResult == 0) {
                return this._compareCharCode(charactor1, charactor2);
            }
            return pinyinCompareResult;
        } else if (!sourceCharactorPinyin && targetCharactorPinyin) {
            return 1;
        } else if (sourceCharactorPinyin && !targetCharactorPinyin) {
            return -1
        } else {
            return 0
        }

    }

    getCharactorFirstPinYinWord(charactor) {
        /**@description
         * 
         * @param 
         * @returns
         * 
         * 
         * false
         */
        for (let key in this.dic) {
            if (this.dic[key].indexOf(charactor[0]) >= 0) {
                return key;
            }
        }

        if (("A" <= charactor[0] && charactor[0] <= "Z") || ("a" <= charactor[0] && charactor[0] <= "z") || "0" <= charactor[0] && charactor[0] <= "9") {
            return charactor[0].toUpperCase();
        }

        return false
    }
    /**
     * checkCharactorIsChinese
     * @description 
     * 
     * 
     * 
     * I am falseI
     *  is metrue
     */
    checkCharactorIsChinese(word) {
        //unicode0x4e0019968
        //0x9fff40959
        //1996840959
        let charactor = word.toString();
        return 19968 <= charactor.charCodeAt(0) && charactor.charCodeAt(0) <= 40959;
    }
}






