import { Pipe, PipeTransform } from '@angular/core';
import { PriceUnitEnum, MsgTypeEnum } from '../../../share_platform/market/enum';
/*
 * Raise the value exponentially
 * Takes an exponent argument that defaults to 1.
 * Usage:
 *   value | exponentialStrength:exponent
 * Example:
 *   {{ 2 | exponentialStrength:10 }}
 *   formats to: 1024
*/
@Pipe({ name: 'msgPipePipe' })
export class MsgPipePipe implements PipeTransform {
    mesType: MsgTypeEnum
    transform(value: MsgTypeEnum, exponent: string) {




        // if (value == MsgTypeEnum.TOBE_PAY) {
        //     return ""
        // }
        switch (value) {
            case MsgTypeEnum.ACC_AMT:
                "";
                break;
            case MsgTypeEnum.ACTOR_CHANGE:
                "";
                break;
            case MsgTypeEnum.CUST_INVITE:
                ""
                break;
            case MsgTypeEnum.REPAY_APPLY:
                "";
                break;

            case MsgTypeEnum.REPAY_CONFIRM:
                "";
                break;
            default:
                ""
        }

    }
}


